/* =============================
   GLOBAL CONTAINER
   ============================= */
.birthday-card-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 20px;
  position: relative;
  overflow: hidden;
  perspective: 2000px; /* Proper 3D effect */
}

/* Animated background */
.birthday-card-container::before {
  content: '';
  position: absolute;
  inset: 0;
  background:
    radial-gradient(circle at 20% 80%, rgba(120, 119, 198, 0.3), transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 119, 198, 0.3), transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(120, 219, 255, 0.2), transparent 50%);
  animation: floatBackground 20s ease-in-out infinite;
  z-index: 0;
}

@keyframes floatBackground {
  0%, 100% { transform: translate(0, 0) scale(1); }
  50% { transform: translate(-10px, -10px) scale(1.02); }
}

/* =============================
   CARD STRUCTURE
   ============================= */
.birthday-card {
  position: relative;
  width: 900px;
  max-width: 95vw;
  height: 550px;
  max-height: 80vh;
  cursor: pointer;
  transform-style: preserve-3d;
  transition: transform 0.9s ease;
  border-radius: 25px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.25);
  z-index: 2;
}

.birthday-card.open {
  transform: rotateY(180deg);
}

.card-front,
.card-inside {
  position: absolute;
  inset: 0;
  display: flex;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  border-radius: 25px;
  overflow: hidden;
}

/* =============================
   FRONT OF CARD
   ============================= */
.card-front {
  background: white;
  display: flex;
}

.front-content {
  display: flex;
  width: 100%;
}

.front-image-section {
  flex: 1.2;
  position: relative;
  overflow: hidden;
}

.image-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.front-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.8s ease;
  filter: brightness(0.95) contrast(1.05);
}

.gradient-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(
    135deg,
    rgba(102, 126, 234, 0.2),
    rgba(118, 75, 162, 0.3)
  );
  pointer-events: none;
}

/* Floating emojis */
.floating-elements {
  position: absolute;
  inset: 0;
  pointer-events: none;
}

.floating-element {
  position: absolute;
  font-size: 26px;
  animation: floatAround 8s infinite ease-in-out;
}

.element-1 { top: 15%; left: 15%; }
.element-2 { top: 65%; left: 75%; animation-delay: 1s; }
.element-3 { top: 35%; left: 35%; animation-delay: 2s; }

@keyframes floatAround {
  0%,100% { transform: translate(0,0); }
  50% { transform: translate(10px, -10px); }
}

.front-text-section {
  flex: 0.8;
  background: #ffffff;
  padding: 50px 30px;
  text-align: center;
}

.front-title {
  font-size: 32px;
  font-weight: 700;
  background: linear-gradient(135deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.front-subtitle {
  font-size: 15px;
  color: #555;
  margin: 15px 0 30px;
}

/* Button */
.cta-button {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  padding: 14px 25px;
  color: white;
  border-radius: 30px;
  font-weight: 600;
  transition: 0.3s;
}

.cta-button:hover {
  transform: scale(1.05);
}

.dot {
  width: 7px;
  height: 7px;
  background: #ccc;
  border-radius: 50%;
  animation: dotPulse 1.5s infinite;
}

@keyframes dotPulse {
  50% { transform: scale(1.6); opacity: 1; }
  0%,100% { opacity: 0.5; }
}

/* =============================
   INSIDE OF CARD
   ============================= */

.card-inside {
  background: #fafafa;
  transform: rotateY(180deg);
  display: flex;
}

/* Left side (photo) */
.inside-left {
  flex: 1;
  padding: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.photo-wrapper {
  width: 100%;
  max-width: 390px;
  height: 240px;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  box-shadow: 0 10px 25px rgba(0,0,0,0.15);
}

.landscape-photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.photo-message h3 {
  margin-top: 15px;
  font-size: 22px;
  color: #333;
}

/* Right side (message) */
.inside-right {
  flex: 1;
  padding: 40px;
  overflow-y: auto;
}

.birthday-title {
  font-size: 36px;
  font-weight: 800;
  background: linear-gradient(135deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.main-message {
  font-size: 15px;
  line-height: 1.7;
  color: #555;
}

.highlight-box {
  background: #ffe0ef;
  border-left: 5px solid #ff8ac8;
  padding: 18px;
  border-radius: 12px;
  margin: 20px 0;
}

.wish-item {
  padding: 12px;
  background: #f5f7fa;
  border-radius: 10px;
  margin-bottom: 10px;
  display: flex;
  gap: 12px;
}

/* Footer */
.signature {
  text-align: center;
  margin-top: 25px;
  color: #444;
}

/* =============================
   CELEBRATION OVERLAY (non-blocking)
   ============================= */

.celebration-overlay {
  position: absolute;
  inset: 0;
  display: flex;
  justify-content: center;
  gap: 30px;
  align-items: center;
  pointer-events: none;
  z-index: 5;
  animation: fadeOut 3s ease forwards;
}

.pop {
  font-size: 45px;
  animation: popAnim 0.6s infinite alternate;
}

@keyframes popAnim {
  from { transform: translateY(0) scale(1); }
  to { transform: translateY(-10px) scale(1.15); }
}

@keyframes fadeOut {
  0% { opacity: 1; }
  100% { opacity: 0; }
}

/* =============================
   RESPONSIVE
   ============================= */

@media (max-width: 768px) {
  .birthday-card {
    height: 450px;
  }
  .front-content {
    flex-direction: column;
  }
  .front-text-section {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .birthday-card {
    height: 380px;
  }
  .front-title {
    font-size: 24px;
  }
  .birthday-title {
    font-size: 28px;
  }
}
